@use "../utils/variables" as *;

.stars {
  display: flex;
  margin: 0;
  padding: 0;

  &__star {
    height: $star-size;
    width: $star-size;
    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
    list-style: none;
  }

  @for $active-stars from 1 through 5 {
    &--#{$active-stars}>:nth-child(-n + #{$active-stars}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
